import styled from 'styled-components'
import React from 'react'
import { withRouteData, Link } from 'react-static'
import groupBy from 'lodash.groupby'
import { markdownToText } from '../util'
import moment from 'moment'

export default withRouteData(({posts}) => {
  const grouped = groupBy(posts, 'date')
  return (
    <Root>
      {
        Object.keys(grouped).sort((left, right) => left < right).map(date => (
          <Wrapper key={date}>
            <header>
              <h3>{moment(date).format('YYYY/MM/DD')}</h3>
            </header>
            <List>
              {
                grouped[date].map(post => (
                  <Post
                    to={`/post/${post.date}/${post.basename}`}
                    key={post.basename}
                  >
                    <header>
                      <h4>
                        {post.title}
                      </h4>
                    </header>
                    <section className="post-content">
                      <p>
                        {
                          markdownToText(post.body)(60)
                        }
                      </p>
                    </section>
                  </Post>
                ))
              }
            </List>
          </Wrapper>
        ))
      }
    </Root>
  )
})

const Root = styled.div`
  margin: 2.3rem;
  @media only screen and (max-width: 825px) {
    margin: 2.3rem 0;
  }
`

const Wrapper = styled.div`
  margin-top: 3rem;

  > header {
    margin-bottom: 3rem;
  }

  header h3 {
    font-size: 2rem;
    color: rgb(177, 177, 177);
    letter-spacing: 0.15rem;
    font-weight: 300;
  }
`

const Post = styled(Link)`
  box-shadow: 2px 2px 7px rgba(0, 0, 0, .15);
  padding: 1rem;
  header h4 {
    font-size: 1.8rem; 
    color: #333;
    font-weight: 500;
  }

  .post-content {
    color: #a0a0a0;
    font-weight: normal;
    font-size: 1.4rem;
  }
`
const List = styled.section`
  display: grid;
  grid-template-columns: repeat(3, 300px);
  justify-content: center;
  grid-column-gap: 2rem;
  grid-row-gap: 2rem;

  @media only screen and (max-width: 1035px) {
    grid-template-columns: repeat(2, 360px);
  }

  @media only screen and (max-width: 825px) {
    grid-template-columns: 80%;
  }
`



